.title {
    margin-inline-start: 130px;
    font-size:40px;
    font-weight: 700;
    text-align: center;
    align-items: center;
}
.daily-item {
    background-color: #f5f5f5;
    border-radius:15px;
    height: 40px;
    margin: 5px;
    display: flex;
    align-items: center;
    font-size: 14px;
    padding:5px 20px;
    box-shadow: 10px -2px 20px 2px rgb(0 0 0 / 30%);
}
.icon-small {
    width: 40px;
}
.day {
    color: #212121;
    flex: 1 1;
    font-weight: 600;
    margin-left: 15px;
}
.description 
{
    flex: 1 1;
    margin-right: 15px;
    text-align: right;
}
